@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Reem+Kufi:wght@400;700&display=swap');


* {
  /* transition: all 0.3s ease; */
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Reem Kufi', sans-serif;
  /* font-size: 1vw;  */

}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  margin: 1vh/1vw;
  margin-bottom: 0;
  margin-top: 0;
}

h2 {
  /* color: #8DADFF; */
  color: #FFB6CC;
}

ul {
  margin: 0px;
  padding: 0px;
}

.header-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  /* background: linear-gradient(45deg, #8DADFF, #B8CCFF); Adjusted gradient background */
  color: #0013C3;
  height: fit-content;
  z-index: 1;
}

.header-title {
  margin-left: 2vw;
  font-size: 24px;
  font-weight: bold;
  font-family: 'Montserrat Alternates', sans-serif;
  color: #e0e3f9;
}

.header-title p {
  margin: 0;
  margin-bottom: 4px;
}

.header-subtitle {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
  font-family: 'Reem Kufi', sans-serif;
}

.header-tagline {
  text-align: left;
  font-size: 18px;
  font-weight: bold;
  font-family: 'Reem Kufi', sans-serif;
  margin-right: 2vw;
  margin-bottom: 0px;
}

.navigation-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* Center the navigation buttons */
  width: 60vw;
  flex-grow: 1;
  align-self: center;
  z-index: 1000;
  /* Adjust the value as needed */
}

.navigation-button {
  text-decoration: none;
  color: #6C63FF;
  font-size: 24px;
  display: inline-block;
  text-align: center;
  flex-grow: 1;
  border-width: 4px;
  border-style: solid;
  border-radius: 4px;
  margin-left: 2vw;
  padding: 1vw;
  background-color: transparent;
  align-items: center;
}

/* Add these styles for the hover effect on the navigation button on all pages */
.navigation-button:hover {
  background-color: #6C63FF;
  color: #fff;
}

.navigation-button.active {
  background-color: #6C63FF;
  color: #fff;
}

.splash-container,
.about-container,
.experience-container,
.projects-container,
.contact-container {
  display: flex;
  flex-direction: column;
  /* height: 100vh; */
  /* background-color: #191970; */
  max-width: 100%;
  min-width: 768px;
  min-height: 100vh;
}

.main-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex: 1;
  background: radial-gradient(circle, rgb(74, 62, 249) 0%, rgba(79, 255, 182, 0.556) 100%),
    url(https://grainy-gradients.vercel.app/noise.svg);
  filter: saturate(105%) contrast(110%) brightness(1);
  /* animation: shiftNoise 10s linear infinite; Adjust the duration and timing function as needed */
}

.splash-main-container {
  min-height: 100%;
  display: flex;
  flex: 1;
  background: rgb(34, 193, 195);
  background: radial-gradient(circle, rgb(74, 62, 249) 0%, rgba(79, 255, 182, 0.556) 100%),
    url(https://grainy-gradients.vercel.app/noise.svg);
  filter: saturate(105%) contrast(110%) brightness(1);
  /* animation: shiftNoise 10s linear infinite; Adjust the duration and timing function as needed */
}

.section-icon{
  width: 1em;
  height: 1em;
  padding-right: 10px;
}

.navigation-button .section-icon{
  width: 1em;
  height: 1em;
  padding-right: 10px;
  filter: brightness(0) saturate(100%) invert(38%) sepia(38%) saturate(4844%) hue-rotate(229deg) brightness(101%) contrast(101%);
}

.navigation-button:hover .section-icon{
  filter: brightness(100%) saturate(0%);
}

.navigation-button.active .section-icon{
  filter: brightness(100%) saturate(0%);
}


.main-content {
  flex: 1;
  padding: 0px;
  /* Add padding or adjust as needed */
  animation: fadeInAnimation ease 0.1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}


