  font-family: 'Reem Kufi', sans-serif;
  background-color: rgba(255, 255, 255, 0.689);
}

button {
  /* width: 100%;  */
  padding: 8px;
  background-color: #6C63FF;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Reem Kufi', sans-serif;
  font-size: 1vw;
  margin: 4px;
}

/* Add styles for button hover effect */
button:hover {
  background-color: #1B1F7D;
}

.filter-buttons{
