}

/* Add this to your existing styles or create a new section for it */
.contact-icons {
  display: flex;
  margin: 10px;
  /* Adjust the margin as needed */
  justify-content: center;
}

.contact-icon {
  width: 2vw;
  /* Adjust the width as needed */
  height: 2vw;
  /* Adjust the height as needed */
  margin: 10px;
  /* Adjust the spacing between icons as needed */
  cursor: pointer;
}

/* Add styles for the tooltips */
.contact-icon:hover::after {
  content: attr(title);
  display: inline-block;
  position: absolute;
  background: #333;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  font-size: 12px;
  margin-top: -30px;
  /* Adjust the distance from the icon as needed */
  margin-left: 10px;
  /* Adjust the distance from the icon as needed */
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
}

.skill-group {
  margin-bottom: 10px;
}

.skill.github{
  background-color: #005a71;
}

.skill {
  position: relative;
  display: inline-block;
  background-color: #534cd6;
  /* border: 4px solid #534cd6; */
  border-radius: 5px;
  padding: 2px 8px;
  margin-right: 6px;
  margin-bottom: 6px;
  overflow: hidden;
  /* font-size: 1vw; */
}

.skill:hover:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #534cd6, #6C63FF);
  background-size: 200% 200%;
  animation: gradientAnimation 1s infinite alternate;
  border-radius: 5px;
  z-index: -1;
  /* Move the gradient behind the text */
}

.skill:hover {
  position: relative;
  /* Set the position to relative for z-index to work */
  z-index: 1;
  /* Ensure the text is above the gradient */
}

.work-xp-tooltip{
  display: flex;
  flex-wrap: wrap;
  max-width: 400px;

}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 100%;
  }

  100% {
    background-position: 100% 0%;
  }
}

